const fetch = require('node-fetch');

export const TOP_PLAYER_DISPLAY_NUMBERS = 5;

const SAVE_PLAYER = 'SAVE_PLAYER';
const SAVE_SCORE = 'SAVE_SCORE';
const GET_TOP_PLAYERS = 'GET_TOP_PLAYERS';

export const actionTypes = {
    SAVE_PLAYER,
    SAVE_SCORE,
    GET_TOP_PLAYERS
}

const PORT = 4001;
const PUBLIC_IP = '18.217.126.170'; // localhost
const urlPrefix = `http://${PUBLIC_IP}:${PORT}`;

const actionProps = {
    [SAVE_PLAYER]: {
        urlPath:  '/savePlayer',
        httpVerb: 'POST'
    },
    [SAVE_SCORE]: {
        urlPath:  '/saveScore',
        httpVerb: 'POST'
    },
    [GET_TOP_PLAYERS]: {
        urlPath: '/getTopPlayers',
        httpVerb: 'GET'
    }
};

export function makeAPICall(action, bodyParams={}, queryParams={}) {
    const httpVerb = actionProps[action].httpVerb;
    const fetchOptions = {
        method: httpVerb,
        headers: {
            'Content-Type': 'application/json',
          },    
    };

    if(httpVerb === 'POST') {
        fetchOptions.body = JSON.stringify(bodyParams);
    }

    let url = urlPrefix + actionProps[action].urlPath;
    if(Object.keys(queryParams).length) {
        url += "?";
        const paramsSuffixes = [];
        for (let queryParamsKey in queryParams) {
            paramsSuffixes.push(queryParamsKey + "=" + queryParams[queryParamsKey]);
        }
        url += paramsSuffixes.join("&");
    }

    console.log(`connecting ${url}`);
    console.log(`fetch body is ${fetchOptions.body}`);
    return fetch(url, fetchOptions).then(res=>res.json());
}