export function randomElements(count) {
  const n = Object.keys(elements).length;
  const pickedElements = {};
  for (let i = 0; i < count; i++) {
    const index = Math.floor(random(0, n));
    const element = Object.keys(elements)[index];
    if(!(element in pickedElements)) {
      pickedElements[element] = elements[element];
    }
  }
  return pickedElements;
}

export function displayValence(valence) {
  return valence > 0 ? "+" + valence : String(valence);
}

export function timeToDisplay(timeStr) {
  // timeStr in yyyy-mm-ddThh:mm:ss.000z format
  if(timeStr === null) {
    return timeStr;
  }

  const TIndex = timeStr.indexOf('T');
  const dateStr = timeStr.substring(0, TIndex);
  const milliSecondIndex = timeStr.indexOf('.');
  const hmsStr = timeStr.substring(TIndex + 1, milliSecondIndex);
  return dateStr + " " + hmsStr;
}