import React from 'react';
import fetch from 'node-fetch';
import {Switch, Route, NavLink, withRouter} from 'react-router-dom';
import './App.css';
import {Target} from './containers/components/Target';
import {StartGame} from './containers/components/StartGame';
import {SaveScore} from './containers/components/SaveScore';
import {PlayerScores} from './containers/components/PlayerScores';
import {EnterPlayerName} from './containers/components/EnterPlayerName';
import {random, buttonColors, randomElements, displayValence} from './utils/utilityFunc';
import {saveScore, getTopPlayers} from './actions/actions';

const maxSimultaneousTargets = buttonColors.length;

class GameApp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      targets: [],
      gameHasStarted: false,
      gameHasEnded: false,
      correctSelections: [],
      incorrectLastClick: "",
      playerName: "",
      finishTime: null
    };
    this.valence = Math.floor(random(-2, 5.1));
    this.intervals = null;
    this.startGame = this.startGame.bind(this);
    this.endGame = this.endGame.bind(this);
    this.onClickStartGameButton = this.onClickStartGameButton.bind(this);
    this.onClickTarget = this.onClickTarget.bind(this);
    this.onClickEnterPlayerNameButton = this.onClickEnterPlayerNameButton.bind(this);
    this.onClickSaveScore = this.onClickSaveScore.bind(this);
  }

  componentWillUnmount() {
    clearInterval(this.intervals);
  }

  startGame() {
    this.intervals = setInterval(function() {
      const targets = this.generateTargets();
      this.setState({targets});
    }.bind(this), 1000);
    this.setState({gameHasStarted: true});
  }

  endGame() {
    const finishTime = new Date();
    this.setState({
      gameHasEnded: true,
      finishTime
    });
    clearInterval(this.intervals);
    this.intervals = null;
  }

  generateTargets() {
    const targets = [];
    const simultaneousTargets = Math.floor(random(0, maxSimultaneousTargets));
    const simultaneousTargetsElements = randomElements(simultaneousTargets);

    let i = 0;
    for (let element in simultaneousTargetsElements) {
      targets.push(<Target 
        element={element} 
        color={buttonColors[i]} 
        onClick={e => this.onClickTarget(element, this.valence, simultaneousTargetsElements[element])}
        key={"target-" + i}
      />);
      i++;
    }
    return targets;
  }

  onClickEnterPlayerNameButton(playerName) {
    this.setState({playerName});
    console.log('user name:' + playerName);
  }

  onClickStartGameButton(e) {
    this.startGame();
  }

  onClickTarget(element, desiredValence, possibleValences) {
    if(desiredValence in possibleValences) {
      const correctSelections = this.state.correctSelections;
      correctSelections.push(element);
      this.setState({
        correctSelections
      });
    } else {
      this.endGame();
      this.setState({
        incorrectLastClick: element,
        targets: []
      });
    }
  }

  onClickSaveScore(e) {
    saveScore(this.state.playerName, this.state.correctSelections.length, this.state.finishTime)
      .then(res => this.props.history.push('/rank'));
  }

  render() {
    const correctUniqueSelection = [...new Set(this.state.correctSelections)];
    const staticDisplay = this.state.gameHasStarted && !this.state.gameHasEnded ? <div>
      <h1 className="top-bar">Please select the elements that can have valence {displayValence(this.valence)}</h1>
      <h1 className="result">You've selected: {correctUniqueSelection.join(", ")}</h1>
      <h2>Your score: {this.state.correctSelections.length}</h2>
    </div> : this.state.gameHasEnded ? <div>
      <h1 className="top-bar">Incorrect: {this.state.incorrectLastClick} cannot have valence {displayValence(this.valence)}</h1>
      <h2>Your final score: {this.state.correctSelections.length}</h2>
      <SaveScore onSaveScore={this.onClickSaveScore}/>
    </div> : <div>
      <EnterPlayerName onSave={this.onClickEnterPlayerNameButton}/>
      <h1 className="top-bar">Chemical Elements Property Challenge</h1>
      <StartGame disabled={!this.state.playerName.length} onClick={this.onClickStartGameButton}/>
    </div>;

    return (
      <div className="GameApp">
        <header className="GameApp-header">
          {staticDisplay}
          {this.state.targets}
        </header>
      </div>
    );
  }
}

const GameAppWithRouter = withRouter(GameApp);

{/*  */}

class NavLinks extends React.Component {
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <NavLink to='/game' className="nav-link">Play</NavLink>
            </li>
            <li className="nav-item">
              <NavLink to='/rank' className="nav-link">See Score Rank</NavLink>
            </li>
          </ul>
        </div>
      </nav>
    )
  }
}

class ScoresRank extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.initialize = this.initialize.bind(this);
  }
  initialize() {
    if(!this.state.initialized) {
      getTopPlayers()
      .then(res => {
        console.log(`response is ${res}`);
        this.setState({topPlayersTable: res, initialized: true});
      })
      .catch(err => {
        console.log(err);
      });
    }
  }
  render() {
    this.initialize();
    return <div className="GameApp">
      <header className="GameApp-header">
        <PlayerScores topPlayersTable={this.state.topPlayersTable}/>
      </header>
    </div>
  }
}

class App extends React.Component {
  render() {
    return (
      <div>
        <NavLinks/>
        <Switch>
          <Route path="/game">
            <GameAppWithRouter/>
          </Route>
          <Route path="/rank">
            <ScoresRank/>
          </Route>
        </Switch>
      </div>
    );
  }
};

export default withRouter(App);
