import {makeAPICall, actionTypes, TOP_PLAYER_DISPLAY_NUMBERS} from '../utils/makeAPICall';

export function savePlayer(playerName) {
    console.log('saving palyer ' + playerName);
    const bodyParams = {name: playerName};
    makeAPICall(actionTypes.SAVE_PLAYER, bodyParams)
        .then(res => {
            console.log(res);
        })
        .catch(err => {
            console.log(err);
        })
}

export function saveScore(playerName, score, finishTime) {
    console.log('saving score ' + score + ' for player ' + playerName);
    const bodyParams = {
        name: playerName, 
        score,
        finish_time: finishTime
    };
    return makeAPICall(actionTypes.SAVE_SCORE, bodyParams)
        .then(res => {
            console.log(res);
        })
        .catch(err => {
            console.log(err);
        })
}

export function getTopPlayers() {
    console.log(`getting top ${TOP_PLAYER_DISPLAY_NUMBERS} players`);
    return makeAPICall(actionTypes.GET_TOP_PLAYERS, {}, {top: TOP_PLAYER_DISPLAY_NUMBERS});
}