import React from 'react';
import {timeToDisplay} from '../../utils/utilityFunc';

export const PlayerScores = (props) => {
    const columns = ['playerName', 'score', 'finishTime'];
    const columnNameMapping = {
        playerName: {
            display: 'Player Name',
            dbName: 'player_name'
        },
        score: {
            display: 'Score',
            dbName: 'score'
        },
        finishTime: {
            display: 'Finish Time',
            dbName: 'finish_time'
        },
    }

    let gridElements = columns.map(c => <div class="col score-rank-header">{columnNameMapping[c].display}</div>);

    for (let idx in props.topPlayersTable) {
        const row = props.topPlayersTable[idx];
        console.log(`This row in top players reads like ${row}`);
        gridElements.push(<div class="w-100"></div>);
        gridElements = gridElements.concat(columns.map(
            c => <div class="col">
                {c === 'finishTime' ? timeToDisplay(row[columnNameMapping[c].dbName]) : row[columnNameMapping[c].dbName]}
            </div>
        ));
    }

    return (
        <div className="container">
            <div className="row">{gridElements}</div>
        </div>
    )
}