import React from 'react';

export const StartGame = (props) => {
  const classNameSpecified = props.disabled ? "start-button-disabled" : "start-button-enabled";
  console.log('button class name: ' + classNameSpecified);
  return <button className={"top-bar start-button " + classNameSpecified} disabled = {props.disabled} onClick={props.onClick}>Start Game</button>;
}