import React from 'react';
import {random} from '../../utils/utilityFunc';

export const Target = (props) => {
  const style = {
    position: 'absolute',
    left: random(10, 90) + "%",
    top: random(10, 90) + "%",
    height: 50,
    width: 50,
    fontSize: 20,
    fontWeight: "bold",
    borderRadius: 15,
    color: "white",
    backgroundColor: props.color
  }
  return <button style={style} onClick={props.onClick}>{props.element}</button>;
}