import React from 'react';
import {savePlayer} from '../../actions/actions';

export class EnterPlayerName extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: ""
        }
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState({value: e.target.value});
    }
    onSubmit(e) {
        savePlayer(this.state.value);
        this.props.onSave(this.state.value);
    }
    render() {
        return (
            <div className="enter-user-name">
                <div className="input-group mb-3">
                    <div class="input-group-prepend">
                        <button class="btn btn-outline-secondary" type="button" onClick={this.onSubmit}>Save Player Name</button>
                    </div>
                        <input type="text" class="form-control" placeholder="Player Name" aria-label="Player Name" aria-describedby="basic-addon1" onChange={this.onChange}/>
                </div>
            </div>

        )
    }
}

